// 
const api = (request) => (name) => {
  const variables = JSON.stringify(name);
  const query = `
    mutation createBookingItemCategory($name:String!){
        createBookingItemCategory (name:$name)
      }
    `;
  return request({
    method: 'post',
    data: { query, variables },
  });
};
export default api;
